import React from 'react';
import {Card, Button} from 'react-bootstrap'
// import T from '../images/music_youtube/jamming.PNG'

function Music(props){
    return (
        <div className="col-lg-3 col-md-4 col-sm-12 col-6">   
            <div className="card-body">
            <Card style={{ width: '11rem' }}>          
            <Card.Img variant="top" src={props.image} />
            <Card.Body>
                <Card.Title>{props.title}</Card.Title>
                <Card.Text>

                {props.content}
                </Card.Text>
                <a href={props.video + ''} target="_blank" rel="noopener noreferrer"><Button variant="primary">Go watch</Button></a>
            </Card.Body>
            </Card>
            </div>
        </div>
    )
}
export default Music;