import React from 'react';
import {Navbar,Nav} from 'react-bootstrap'
import {Link} from "react-router-dom";
import * as ROUTES from '../constants/routes'

const NavBar = ()=> (

            <div className="topnav" id="myTopnav">
            <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
                <Navbar.Brand style={{color:"red"}} href="/">WI</Navbar.Brand>
                <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                <Navbar.Collapse id="responsive-navbar-nav">
                    <Nav className="mr-auto">
                    <Link to={ROUTES.LANDING}><Nav.Link href={ROUTES.LANDING} >About</Nav.Link></Link>
                    <Link to={ROUTES.RESUME}><Nav.Link href={ROUTES.RESUME} >Resume</Nav.Link></Link>
                    <Link to={ROUTES.PROJECTS}><Nav.Link href={ROUTES.PROJECTS} >Projects</Nav.Link></Link>
                    <Link to={ROUTES.CONTACT}><Nav.Link href={ROUTES.CONTACT}>Contact</Nav.Link></Link>
                   
                    </Nav>
                </Navbar.Collapse>
            </Navbar>
            

        </div>
    )


export default NavBar;