import React from 'react';
import {Media} from 'react-bootstrap'

function Computer(props){
    return(
        <div className="container">   
        <Media>
            <img
                width={64}
                height={64}
                className="mr-3"
                src={props.image}
                alt="Generic placeholder"
            />
            <Media.Body>
                <h5>{props.title}</h5>
                <p>
                {props.content}
                </p>
            </Media.Body>
        </Media>
        </div>
    )
}
export default Computer;