import React, { Component } from 'react';
import '../style/projects.css'
import {Container,Image,Row,Col} from 'react-bootstrap'
import SplitText from 'react-pose-text';

class About extends Component {
    constructor(props) {
        super(props);
        this.state = {  };
    }
    render() {

        const charPoses = {
            exit: { opacity: 0, y: 20 },
            enter: {
              opacity: 1,
              y: 0,
              delay: ({ charIndex }) => charIndex * 100
            }
          };

        return (
        <div className="About">
            <Container>
                <Row>
                    <Col xs={12} sm={6} lg={4}>
                        <Image fluid src="/me_with_bg.png" />
                    </Col>
                    <Col xs={12} sm={6} lg={8}>
                        <div className='about_text'>
                            {/* <h1>
                                {timeOfDay}!!
                            </h1> */}
                            <h2>
                                <SplitText initialPose="exit" pose="enter" charPoses={charPoses}>
                                    My name is Tom Issarakoat
                                </SplitText>
                            </h2>
                            <h5>
                                
                                I am currently a Computer Science Student at the University of Utah, 
                                set to graduate in May 2020. <br/>Before I went to school for computer science I worked as a Jazz musician, 
                                music composer, music recorder/mixer, and jazz guitar teacher.
                            <br/>In my free time I like to spend time with my family, make music, cook, skateboard and golf
                            <br/>
                            <a 
                            href="https://indd.adobe.com/view/cb510d2c-1647-4032-a58e-3f83ea26da9e" 
                            target="_blank" 
                            rel="noopener noreferrer">
                                If you like jazz music, Let me tell you my Jazz story</a></h5>
                        </div>
                    </Col>
                </Row>
            </Container>
        </div>
        );
    }
}

export default About;