import React, { Component } from 'react';
import '../style/projects.css'
import Computer from './Computer'
import Music from './Music';
import MusicData from '../data/MusicData';
import ComputerData from '../data/ComputerData';
import Film from './Films'

class Projects extends Component {
    constructor(props, context) {
        super(props, context);
    
        this.state = {
          open: false,
        };
      }
    
      render() {
        // const { open } = this.state;
        const MusicComponent = MusicData.map(music=>
          <Music key={music.id} title={music.title} content={music.content} image={music.image} video={music.video} />)
        const ComputerComponent = ComputerData.map(c => 
          <Computer key={c.id} title={c.title} content={c.content} image={c.image}/>)
        return (
            <div className="accordion" id="accordionExample">
            <div className="card">
              <div className="card-header bg-button" id="headingOne">
                <h2 className="mb-0">
                  <button className="btn btn-info" type="button" data-toggle="collapse" data-target="#Computer" aria-expanded="true" aria-controls="Computer">
                    Computer Programming
                  </button>
                </h2>
              </div>
          
              <div id="Computer" className="collapse show" aria-labelledby="headingOne" data-parent="#accordionExample">
                <div className="card-body bg_c">
                    {ComputerComponent}
                </div>
              </div>
            </div>
            <div className="card">
              <div className="card-header bg-button" id="headingTwo">
                <h2 className="mb-0">
                  <button className="btn collapsed btn-info" type="button" data-toggle="collapse" data-target="#Music" aria-expanded="false" aria-controls="Music">
                    Music Composing and performing
                  </button>
                </h2>
                <div ><p><a href="https://www.youtube.com/user/issarakoat/featured?view_as=subscriber" target="_blank" rel="noopener noreferrer">Visit youtube chanal for more videos</a></p></div>
              </div>
              <div id="Music" className="collapse" aria-labelledby="headingTwo" data-parent="#accordionExample">
              <div class="row text-center text-lg-left">

                  {MusicComponent}
     

              </div>
              </div>
            </div>
            <div className="card">
              <div className="card-header bg-button" id="headingThree">
                <h2 className="mb-0">
                  <button className="btn collapsed btn-info" type="button" data-toggle="collapse" data-target="#Films" aria-expanded="false" aria-controls="Films">
                    Films
                  </button>
                </h2>
              </div>
              <div id="Films" className="collapse" aria-labelledby="headingThree" data-parent="#accordionExample">
                <div className="card-body darkBG">
                <Film />
                </div>
              </div>
            </div>
          </div>
        );
      }
    }

export default Projects;